<template>
  <div class="personal-content-body">
    <div class="applytempbox">
      <div class="table-content" :class="{'table-content-hide':showright}">
        <div class="clear table-content-title">
          <div class="left" v-if="edittype==1">新增审批条件</div>
          <div class="left" v-else>修改审批条件</div>
        </div>
        <div class="table-group">
          <div class="table-group-content">
            <div class="clear table-group-title">
              <div class="left breadcrumb">
                <el-breadcrumb separator-class="el-icon-arrow-right">
                  <el-breadcrumb-item v-for="(item,index) in routeList" :key="index" :to="{name:item.name,query:item.query}">{{item.name_str}}</el-breadcrumb-item>
                </el-breadcrumb>
              </div>
              <div class="line"></div>
            </div>
            <div class="applytemppro_box">
              <div classs="atp_group">
                <div class="atp_group_title">
                  当审批同时满足以下条件时
                </div>
                <div class="atp_group_content">
                  <div class="atp_peoplelist">
                    <div class="atp_peopleitem marginb_30" v-if="typeIdList.indexOf(1)!=-1">
                      <div class="atp_peopleitem_left">发起人</div>
                      <div class="atp_peopleitem_right atp_peopleitem_right_hasdelete">
                        <div class="atp_peopleitem_addeditem marginr_10" v-if="fqList.length!=0" v-for="(item,index) in fqList">
                          <span class="iconfont icon-guanbi" @click="deleteUser('fq',item.id,index)"></span>
                          <div class="atp_peopleitem_addeditem_top">
                            <div class="atp_peopleitem_peoplebox">
                              <img :src="item.url" @error="getImgurl" />
                              <span>{{item.name}}</span>
                            </div>
                          </div>
                        </div>
                        <div class="atp_peopleitem_text alink" @click="addSpPeople">编辑</div>
                      </div>
                      <div class="atp_peopleitem_deletelink" @click="deleteCondition(1)">删除该条件</div>
                    </div>
                    <div class="atp_peopleitem marginb_30" v-if="typeIdList.indexOf(2)!=-1">
                      <div class="atp_peopleitem_left">项目</div>
                      <div class="atp_peopleitem_right atp_peopleitem_right_hasdelete">
                        <div class="atp_peopleitem_inputbox">
                          <el-select v-model="proId" placeholder="请选择" size="small" multiple>
                            <el-option v-for="data in proList" :key="data.id" :label="data.name" :value="data.id">
                            </el-option>
                          </el-select>
                        </div>
                        <div class="atp_peopleitem_deletelink" @click="deleteCondition(2)">删除该条件</div>
                      </div>
                    </div>
                    <div class="atp_peopleitem marginb_30" v-if="typeIdList.indexOf(4)!=-1">
                      <div class="atp_peopleitem_left">基金</div>
                      <div class="atp_peopleitem_right atp_peopleitem_right_hasdelete">
                        <div class="atp_peopleitem_inputbox">
                          <el-select v-model="fundId" placeholder="请选择" size="small" multiple>
                            <el-option v-for="data in fundList" :key="data.id" :label="data.name" :value="data.id">
                            </el-option>
                          </el-select>
                        </div>
                        <div class="atp_peopleitem_deletelink" @click="deleteCondition(4)">删除该条件</div>
                      </div>
                    </div>
                    <div class="atp_peopleitem marginb_30">
                      <div class="atp_peopleitem_left"></div>
                      <div class="atp_peopleitem_right">
                        <div class="atp_peopleitem_right_link" @click="chooseCondition"><span class="fsize_18">+</span>更多条件</div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div classs="atp_group">
                <div class="atp_group_title">对应审批人</div>
                <div class="atp_group_content">
                  <div class="atp_peoplelist">
                    <div class="atp_peopleitem">
                      <div class="atp_peopleitem_left">审批人</div>
                      <div class="atp_peopleitem_right">
                        <draggable v-model="applierList" :options="{group:{ name:'control' }}">
                          <transition-group tag="div" class="flex flex_wrap" id="apply">
                            <div class="flex" v-for="(item,index) in applierList" :key="index">
                              <div class="atp_peopleitem_addeditem" @click="editApplyPeople(index,item)">
                                <span class="iconfont icon-guanbi" @click.stop="deleteApplyPeople(index)"></span>
                                <div class="atp_peopleitem_addeditem_top">
                                  <div class="atp_peopleitem_peoplebox" v-if="item.class==1">
                                    <img src="../../../static/images/appltTemp_icon.png" />
                                    <span>主管理员</span>
                                  </div>
                                  <div class="atp_peopleitem_peoplebox" v-for="user in item.user" v-if="item.class==2||item.class==3">
                                    <img v-if="item.class==2" :src="user.url" />
                                    <img v-else src="../../../static/images/appltTemp_icon.png" />
                                    <span>{{user.name}}</span>
                                  </div>
                                </div>
                                <div class="atp_peopleitem_addeditem_bottom" v-if="item.name!=''">
                                  {{item.name}}
                                </div>
                              </div>
                              <div class="atp_peopleitem_icon marginlr_10"><span class="iconfont icon-xiajiantou"></span></div>
                              <div class="atp_peopleitem_icon" @click="addApplyPeople" v-if="index==applierList.length-1">
                                <div class="atp_peopleitem_addicon"></div>
                              </div>
                            </div>
                          </transition-group>
                        </draggable>
                        <div class="atp_peopleitem_icon" @click="addApplyPeople" v-if="applierList.length==0">
                          <div class="atp_peopleitem_addicon"></div>
                        </div>
                      </div>
                    </div>
                    <div class="atp_peopleitem" v-if="type==2||type==3">
                      <div class="atp_peopleitem_left">经办人</div>
                      <div class="atp_peopleitem_right">
                        <div class="atp_peopleitem_addeditem marginr_10" v-if="operatorList.length!=0" v-for="(item,index) in operatorList">
                          <span class="iconfont icon-guanbi" @click="deleteUser('operator',item.id,index)"></span>
                          <div class="atp_peopleitem_addeditem_top">
                            <div class="atp_peopleitem_peoplebox">
                              <img :src="item.url" @error="getImgurl" />
                              <span>{{item.name}}</span>
                            </div>
                          </div>
                        </div>
                        <!-- <div class="atp_peopleitem_text">未设置（不抄送给任何人)</div> -->
                        <div class="atp_peopleitem_icon" @click="addOperatorPeople">
                          <div class="atp_peopleitem_addicon"></div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="full_line"></div>
              <div class="btn_box">
                <div class="btn-blue" @click="commitCondition">保存</div>
                <!-- <div class="btn-blue-border">流程检查</div> -->
              </div>
            </div>
          </div>
        </div>
        <add-people-modal :visibleDialog="fqVisibleDialog" name="fq" title="选择发起人" :selectedId="fqIdList" @changeshow="changeModalShow" @setData="getModaldata"></add-people-modal>
        <add-people-modal :visibleDialog="operatorVisibleDialog" name="operator" title="选择经办人" :selectedId="operatorIdList" @changeshow="changeModalShow" @setData="getModaldata"></add-people-modal>
      </div>
      <apply-temp-right :showright="showright" @changeshow="changerightshow" @setData="setApplyData" :data="applierItem"></apply-temp-right>
      <!--  选择条件-->
      <el-dialog class="choosePeopleDialog" title="选择条件" :visible.sync="chooseConditionDialog" width="600px" center :append-to-body='true'>
        <div class="dialog_content">
          <div class="marginb_20">请选择用来区分审批流程的条件字段</div>
          <el-checkbox-group v-model="dialogTypeIdList">
            <el-checkbox v-for="type in typeList" :label="type.id" :key="type.id">{{type.name}}</el-checkbox>
          </el-checkbox-group>
        </div>
        <div slot="footer" class="dialog_footer clear">
          <button class="dialog-btn-submit btn-blue" @click="commitChooseCondition">确定</button>
          <button class="dialog-btn-submit btn-blue-border " @click="chooseConditionDialog=false">取消</button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
//审批流程状态
import { dialogConfirm, compareJson, dialogConfirmHasbtn, setStore, getStore, removeStore } from '../../config/mUtils'
import { allProjectOrFund, getUserCompany, applyTempEditSingleCon, applyTempSaveSingleCon } from '../../config/api'
import applyTempRight from './applyTempRightNew.vue' //右部模块
import addPeopleModal from '../applyManage/addPeopleModal.vue' //右部模块
import draggable from 'vuedraggable'
export default {
  data() {
    return {
      id: '',
      routeList: [],
      template_id: 2,
      edittype: 1,
      type: 1,
      showright: false,
      //用户列表
      allVisibleStr: [],
      //项目
      proList: [{
        id: 1,
        name: '测试'
      }, {
        id: 2,
        name: '测试1'
      }],
      proId: [],
      fundList: [],
      fundId: [],
      fundList: [],
      //选择条件弹窗
      chooseConditionDialog: false,
      //可选条件
      typeList: [{
        id: 1,
        name: '发起人'
      }, {
        id: 2,
        name: '项目'
      }, {
        id: 4,
        name: '基金'
      }],
      typeIdList: [1, 2, 4],
      dialogTypeIdList: [1, 2, 4],
      //右侧弹窗数据
      applierItem: {
        name: '',
        class: 1, //审批类别
        pattern: 1, //审批方式
        uid: [],
        uidList: [],
      },
      //审批人列表
      applierList: [],
      ////////////////////条件列表
      conditionList: [],
      //抄送人弹窗///////////////////////////////
      copierVisibleDialog: false,
      //抄送人列表
      copierList: [],
      copierIdList: [],
      //发起人弹窗///////////////////////////////
      fqVisibleDialog: false,
      //发起人列表
      fqList: [],
      fqIdList: [],
      // defaultExpandedArr:[],
      //经办人人弹窗///////////////////////////////
      operatorVisibleDialog: false,
      //经办人列表
      operatorList: [],
      operatorIdList: [],
      // defaultExpandedArr:[],
    }
  },
  created() {
    this.getProOrfund();
    if (this.routeList.length != 0) {} else {
      this.routeList = this.$route.meta.routeList;
    }
    if (this.$route.query.edittype != undefined && this.$route.query.type != undefined && this.$route.query.template_id != undefined && this.$route.query.edittype != '' && this.$route.query.type != '' && this.$route.query.template_id != '') {
      this.edittype = this.$route.query.edittype;
      this.type = this.$route.query.type;
      this.template_id = this.$route.query.template_id;
    } else {
      this.$router.push({
        name: 'applytempList'
      })
    }
    // if (this.type == 2 || this.type == 3) {
    //   this.typeList = [{
    //     id: 1,
    //     name: '发起人'
    //   }, {
    //     id: 2,
    //     name: '项目'
    //   }, {
    //     id: 4,
    //     name: '基金'
    //   }];
    //   this.typeIdList = [1, 2, 4];
    // } else {
    //   this.typeIdList = [1];
    //   this.typeList = [{
    //     id: 1,
    //     name: '发起人'
    //   }]
    // }
    if (this.edittype == 1) {} else {
      let htac = JSON.parse(getStore('htcx_applyCondition'));
      //let typeIdList = htac.type.toString().split(',');
      //this.typeIdList = this.changeArrayString(typeIdList);
      this.id = htac.id;
      this.applierList = htac.sp;
      for(let i in htac.condition){
        switch (i) {
          case '1':
            this.fqIdList = this.changeArrayString(htac.condition[i]);
            break;
          case '2':
            this.proId = this.changeArrayString(htac.condition[i]);
            break;
          case '4':
            this.fundId = this.changeArrayString(htac.condition[i]);
            break;
        }
      }
      this.operatorList = [];
      this.operatorIdList = [];
      if (htac.handle.user == undefined || htac.handle.user.length == 0) {} else {
        this.operatorList = htac.handle.user;
        this.operatorList.forEach(item => {
          this.operatorIdList.push(item.id);
        })
      }
    }
    this.getUserRule();
  },
  components: {
    applyTempRight,
    draggable,
    addPeopleModal
  },
  computed: {},
  mounted() {},
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    }
  },
  methods: {
    commitCondition() {
      let param = {};
      let paramtype = [];
      let condition = {};
      if (this.typeIdList.length == 0) {
        dialogConfirm('info', '条件不能为空');
        return;
      } else {
        this.typeIdList.forEach(item => {
          if (item == 1) {
            if (this.fqIdList.length != 0) {
              condition[1] = this.fqIdList.toString();
              paramtype.push(1);
            }
          } else if (item == 2) {
            if (this.proId.length != 0) {
              condition[2] = this.proId.toString();
              paramtype.push(2);
            }
          } else if (item == 4) {
            if (this.fundId.length != 0) {
              condition[4] = this.fundId.toString();
              paramtype.push(4);
            }
          }
        })
        if (paramtype.length == 0) {
          dialogConfirm('info', '条件不能为空');
          return;
        }
      };
      let splist = [];
      this.applierList.forEach(item => {
        let sp = {};
        sp.name = item.name;
        sp.uid = item.uid.toString();
        sp.class = item.class;
        sp.pattern = item.pattern;
        splist.push(sp);
      })
      param.sp = splist;
      if (this.type == 2 || this.type == 3) {
        param.handle = this.operatorIdList.toString();
      }
      param.condition = condition;
      param.type = paramtype.toString();
      if (this.edittype == 2) {
        applyTempEditSingleCon({
          id: this.id,
          data: JSON.stringify(param)
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '提交成功');
            this.$router.push({
              name: 'applytempProcessNew',
              query: {
                template_id: this.template_id,
                type: this.type
              }
            })
          } else {
            dialogConfirm('fail', res.data.message);
          }
        })
      } else if (this.edittype == 1) {
        applyTempSaveSingleCon({
          tid: this.template_id,
          data: JSON.stringify(param)
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '提交成功');
            this.$router.push({
              name: 'applytempProcessNew',
              query: {
                template_id: this.template_id,
                type: this.type
              }
            })
          } else {
            dialogConfirm('fail', res.data.message);
          }
        })
      }
    },
    changeArrayString(string) {
      let array = string.toString().split(',');
      let arrayInt = [];
      array.forEach(item => {
        arrayInt.push(parseInt(item))
      })
      return arrayInt;
    },
    //获取用户权限信息
    getUserRule() {
      getUserCompany({
        is_all: 1
      }).then(res => {
        if (res.data.msgNo == 1) {
          let vl = res.data.payload;
          let allVisibleStr = [];
          vl.forEach(item => {
            if (item.children != undefined) {
              item.children.forEach(item1 => {
                allVisibleStr.push(item1);
              })
            }
          })
          this.fqList = allVisibleStr.filter(item2 => {
            return this.fqIdList.indexOf(item2.id) != -1
          })
          this.allVisibleStr = allVisibleStr;
        } else {
          dialogConfirm('fail', res.data.message);
        }
      })
    },
    //修改选择条件
    commitChooseCondition() {
      this.chooseConditionDialog = false;
      this.typeIdList = this.dialogTypeIdList;
      let il = [1, 2, 4];
      let ilchild = il.filter(item => {
        return this.typeIdList.indexOf(item) == -1
      })
      ilchild.forEach(item1 => {
        this.clearCondition(item1)
      })
    },
    deleteCondition(id) {
      this.typeIdList.splice(this.typeIdList.indexOf(id), 1);
      this.clearCondition(id);
    },
    clearCondition(id) {
      if (id == 1) {
        this.fqList = [];
        this.fqIdList = [];
      } else if (id == 2) {
        this.proId = [];
      } else if (id == 4) {
        this.fundId = [];
      }
    },
    chooseCondition() {
      this.chooseConditionDialog = true;
      this.dialogTypeIdList = this.typeIdList;
    },
    //获取项目/基金列表
    getProOrfund() {
      allProjectOrFund({
        type: 1
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.proList = res.data.payload.data;

        }
      })
      allProjectOrFund({
        type: 2
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.fundList = res.data.payload.data;
        }
      })
    },
    //新增审批人
    addApplyPeople() {
      this.showright = true;
      this.applierItem = {
        rightType: 1,
        name: '',
        class: 1, //审批类别
        pattern: 2, //审批方式
        uid: [],
        user: [],
      };
    },
    //修改审批人
    editApplyPeople(index, item) {
      this.applierItem = item;
      this.applierItem.rightType = 2;
      this.applierItem.rightIndex = index;
      this.showright = true;
    },
    //删除审批人
    deleteApplyPeople(index) {
      this.applierList.splice(index, 1)
    },
    //右侧数据传递
    changerightshow(type) {
      this.showright = type;
    },
    //新增默认审批人
    setApplyData(data) {
      let newdata = {};
      newdata.class = data.class;
      newdata.name = data.name;
      newdata.pattern = data.pattern;
      newdata.uid = data.uid;
      newdata.user = data.user;
      if (data.rightType == 1) {
        this.applierList.push(newdata);
      } else {
        this.applierList[data.rightIndex] = newdata;
      }
      this.showright = false;
    },

    //新增/修改经办人
    addOperatorPeople() {
      this.operatorVisibleDialog = true;
    },
    //新增/修改发起人
    addSpPeople() {
      this.fqVisibleDialog = true;
    },
    //删除抄送人/经办人
    deleteUser(name, id, index) {
      let name1 = name + 'List';
      let name2 = name + 'IdList';
      this[name1].splice(index, 1);
      this[name2] = this[name2].filter(item => {
        return item != id
      });
    },
    ////////////////////////////可见配置弹窗//////////////////////
    ///////////////////////////弹窗数据传递
    //修改弹窗显示
    changeModalShow(type, name) {
      this[name + 'VisibleDialog'] = type
    },
    getModaldata(name, list) {
      this[name] = list;
    },
    getImgurl(e) {
      e.currentTarget.src = '../../static/images/header_userimg.png';
      e.onerror = null;
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';

</style>



