<template>
	<!--  设置权限弹窗-->
	<el-dialog class="choosePeopleDialog" :title="title" :visible.sync="visibleDialog" width="800px" center :append-to-body='true' :before-close="visibleClose">
		<div class="dialog_content flex">
			<div class="dialog_content_content">
				<div class="dialog_content_content_title">选择人员</div>
				<div>
					<el-input placeholder="输入关键字进行过滤" v-model="filterText">
						<i slot="prefix" class="el-input__icon el-icon-search"></i>
					</el-input>
				</div>
				<!--<div class="custom_tree_node flex">
					<el-checkbox v-model="visibleCheckedall" @change="changeVisibleCheckedall">全选</el-checkbox>
				</div>-->
				<el-tree class="dialog_content_content_tree" :data="visibleList" show-checkbox node-key="id" :default-checked-keys="defaultChechedArr" :filter-node-method="filterNode" ref="tree" :check-on-click-node="true" :props="defaultProps" @check="visivleListChange">
					<span class="custom_tree_node flex" slot-scope="{ node, data }">
              			<img :src="node.data.url"  @error="getImgurl" v-if="node.data.url!=undefined">
              			{{node.data.name}}
           	 		</span>
				</el-tree>
			</div>
			<div class="dialog_content_content">
				<div class="dialog_content_content_title">已选人员</div>
				<div class="selected_list">
					<div class="selected_item" v-for="people in checkedVisibleStr">
						<img class="selected_item_headimg" :src="people.url" @error="getImgurl">{{people.name}}
						<i class="el-icon-error" @click="deleteCheckedVisivle(people.id)"></i></div>
				</div>
			</div>
		</div>
		<div slot="footer" class="dialog_footer clear">
			<button class="dialog-btn-submit btn-blue" @click="commitVisible">确定</button>
			<button class="dialog-btn-submit btn-blue-border " @click="visibleClose">取消</button>
		</div>
	</el-dialog>
</template>
<script>
	import { getUserCompany, getCompanyDepartmentApi, visibleRange } from '../../config/api'
	import { dialogConfirm, compareJson, dialogConfirmHasbtn } from '../../config/mUtils'
	export default {
		name: 'draglist',
		data() {
			return {
				template_id: '',
				filterText: '', //人员筛选
				visibleList: [], //权限列表
				defaultProps: {
					children: 'children',
					label: 'name'
				},
				visibleCheckedall: false,
				checkedVisibleStr: [],
				defaultChechedArr: [], //所有用户数组id
				allVisibleStr: [],
			}
		},
		props: ['visibleDialog', 'selectedId', 'name', 'title'],
		watch: {
			filterText(val) {
				this.$refs.tree.filter(val);
			},
			visibleDialog() {
				this.getUserRule(this.selectedId);
			},
		},
		mounted() {
			//this.getUserRule(this.selectedId);
		},
		methods: {
			//给员工的id加一个标识
			addUnionId(data){
				for(var i in data){
					if (data[i].children) {
						this.$set(data[i],'disabled',true);
						this.addUnionId(data[i].children)
					} else{
						if (data[i].is_user) {
							data[i].id = data[i].id + '-' + data[i].one_department_id;
						} else {
							this.$set(data[i],'disabled',true);
						}
					}
				}
				return data
			},
			
			
			changeshow(type) {
				this.$emit('changeshow', type, this.name);
			},
			////////////////////////////可见配置弹窗//////////////////////
			getImgurl(e) {
				e.currentTarget.src = '../../static/images/header_userimg.png';
				e.onerror = null;
			},
			//获取用户权限信息
			getUserRule(visible) {
				getCompanyDepartmentApi({
					type: 2
				}).then(res => {
					if(res.data.msgNo == 1) {
						var departmentData = this.addUnionId(res.data.payload);
						this.visibleList = departmentData;
						let vl = departmentData;
						let allVisibleStr = [];
						vl.forEach(item => {
							item.id = -item.id;
							if(item.children == undefined || item.children.length == 0) {
								item.disabled = true
							}
							if(item.children != undefined) {
								item.children.forEach(item1 => {
									allVisibleStr.push(item1.id);
								})
								this.checkedVisibleStr = item.children.filter(i => {
									return visible.indexOf(i.id) != -1
								})
							}
						})
						this.allVisibleStr = allVisibleStr;
						this.visibleList = vl;
						if(visible.length != 0) {
							//visible = visible.toString().split(',');
							if(visible.length == allVisibleStr.length) {
								this.visibleCheckedall = true;
							} else {
								this.visibleCheckedall = false;
							}
							this.setCheckedKeyStr(visible);
							this.$refs.tree.setCheckedKeys(visible);
						} else {
							this.visibleCheckedall = false;
							this.$refs.tree.setCheckedKeys([]);
						}
					} else {
						dialogConfirm('fail', res.data.message);
					}
				})
			},
			//人员筛选
			filterNode(value, data) {
				if(!value) return true;
				return data.name.toString().indexOf(value) !== -1;
			},
			//全选/全不选
			changeVisibleCheckedall(e) {
				if(e) {
					this.$refs.tree.setCheckedKeys(this.allVisibleStr);
					this.setCheckedKeyStr(this.allVisibleStr);
				} else {
					this.$refs.tree.setCheckedKeys([]);
					this.setCheckedKeyStr([]);
				}
			},
			//选中人员变化
			visivleListChange(item1, item2) {
				let newArr = [];
				item2.checkedNodes.forEach(element => {
					if(element.children == undefined) {
						let newObj = {};
						newObj.id = element.id;
						newObj.url = element.url;
						newObj.name = element.name
						newArr.push(newObj);
					}
				});
				this.checkedVisibleStr = newArr;
				if(this.checkedVisibleStr.length == this.allVisibleStr.length) {
					this.visibleCheckedall = true;
				} else {
					this.visibleCheckedall = false;
				}
			},
			//获取已选中人员
			setCheckedKeyStr(visible) {
				let vl = this.visibleList;
				let cvs = [];
				vl.forEach(item => {
					if(item.children != undefined) {
						cvs = cvs.concat(item.children.filter(item1 => {
							return visible.indexOf(item1.id.toString()) != -1 || visible.indexOf(item1.id) != -1
						}))
					}
				})
				this.checkedVisibleStr = cvs;
			},
			compare(property) {
				return function(a, b) {
					var value1 = a[property];
					var value2 = b[property];
					return value1 - value2;
				}
			},
			deleteCheckedVisivle(id) {
				this.checkedVisibleStr.forEach((element, index) => {
					if(id == element.id) {
						this.checkedVisibleStr.splice(index, 1);
					}
				});
				let newArr = [];
				this.checkedVisibleStr.forEach((element, index) => {
					newArr.push(element.id);
				});
				if(this.checkedVisibleStr.length == this.allVisibleStr.length) {
					this.visibleCheckedall = true;
				} else {
					this.visibleCheckedall = false;
				}
				this.$refs.tree.setCheckedKeys(newArr);
			},
			//设置权限弹窗确定按钮
			commitVisible() {
				let str = [];
				this.checkedVisibleStr = this.decodeSelectedId(this.checkedVisibleStr);
				
				this.checkedVisibleStr.forEach(item => {
					str.push(item.id);
				})
				this.$emit('setData', this.name + 'List', this.checkedVisibleStr);
				this.$emit('setData', this.name + 'IdList', str);
				this.$emit('getData', this.checkedVisibleStr, str);
				this.filterText = '';
				this.changeshow(false);
			},
			// 给所选择的人员的id进行解码
			decodeSelectedId(data){
				var ids = [];
				for (var i in data) {
					ids = data[i].id.split('-');
					data[i].id = ids[0];
				}
				return data
			},

			//关闭弹窗之前调用
			visibleClose() {
				this.template_id = '';
				this.checkedVisibleStr = [];
				this.$refs.tree.setCheckedKeys([]);
				this.visibleCheckedall = false;
				this.changeshow(false);
			}
		}
	}
</script>
<style lang="scss" scoped>
	@import '../../style/apply';
</style>


